:root {
  --white: #ffffff;
  --rich-black: #013243;
  --black: #000000;
  --cyan: #86bfd3;
  --grey: #bdbdbe;
}

:root {
  --font-roboto: "Roboto", sans-serif;
}

body {
  font-family: var(--font-roboto);
}

body {
  display: grid;
  justify-content: center;
  align-content: center;
  align-items: center;
  min-width: fit-content;
}
@media only screen and (min-width: 1200px) {
  body {
    text-align: center;
    height: 100vh;
  }
}
body .wrapper {
  display: grid;
  overflow: hidden;
}
@media only screen and (min-width: 320px) and (max-width: 480px) {
  body .wrapper {
    grid-template-rows: repeat(2, 1fr);
  }
}
@media only screen and (min-width: 1200px) {
  body .wrapper {
    margin: auto;
    border-radius: 17px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    width: 52%;
    height: 67vh;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-roboto);
  background: none;
}
@media only screen and (min-width: 1200px) {
  body {
    background-image: url("/images/Background.png");
    background-size: 150%;
  }
}

fieldset {
  border: none;
}

.login-page {
  background-color: var(--white);
}
.login-page .header__title {
  font-size: 2rem;
  font-weight: 700;
}
.login-page .cred label {
  font-size: 1rem;
  font-weight: 700;
}
.login-page .cred input {
  background-color: var(--white);
  border-radius: 7px;
  font-size: medium;
  border: none;
  outline: solid;
  outline-width: 1px;
  outline-color: var(--grey);
}

fieldset {
  margin: 0;
  padding: 0;
}

.login-page {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  text-align: start;
  overflow: hidden;
  padding: 2rem 3rem;
}
@media only screen and (min-width: 1200px) {
  .login-page {
    grid-column: 2;
  }
}
@media only screen and (min-width: 320px) and (max-width: 480px) {
  .login-page {
    grid-row: 2;
  }
}
.login-page .header {
  padding-bottom: 1rem;
}
.login-page .cred__email {
  padding-bottom: 1rem;
}
.login-page .cred label {
  line-height: 2.5rem;
}
.login-page .cred input {
  display: block;
  height: 3.5rem;
  width: 17rem;
  padding-left: 1rem;
}

.phone {
  background-color: var(--rich-black);
}
.phone__header {
  color: var(--white);
}
.phone__header .htitle {
  font-size: 3rem;
  font-weight: bold;
}
.phone__header .htext {
  color: var(--cyan);
}

.phone {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media only screen and (min-width: 1200px) {
  .phone {
    grid-column: 1;
  }
}
@media only screen and (min-width: 320px) and (max-width: 480px) {
  .phone {
    grid-row: 1;
  }
}
.phone__header {
  text-align: center;
  padding-top: 3rem;
  padding-bottom: 5rem;
}
@media only screen and (min-width: 320px) and (max-width: 480px) {
  .phone__image {
    max-height: 45vh;
  }
}
@media only screen and (min-width: 1200px) {
  .phone__image {
    height: 100%;
  }
}
@media only screen and (min-width: 1200px) {
  .phone__image img {
    height: 100%;
    width: 70%;
  }
}
@media only screen and (min-width: 320px) and (max-width: 480px) {
  .phone__image img {
    height: 45vh;
    width: 83vw;
  }
}

.login-page__t-c {
  padding: 1.5rem 0.1rem;
  display: flex;
  flex-direction: row;
}
.login-page__t-c .txt {
  padding-left: 5px;
  font-size: 1.1rem;
}
.login-page__t-c .txt a {
  color: var(--rich-black);
  font-weight: 700;
}
@media only screen and (min-width: 1200px) {
  .login-page__t-c .txt {
    font-size: 1.13rem;
  }
}
.login-page__t-c .check {
  padding-right: 3px;
}
.login-page__t-c .check input[type=checkbox] {
  appearance: none;
  background-color: var(--white);
  margin: 0;
  font: inherit;
  width: 1.15em;
  height: 1.15em;
  border: 1px solid var(--grey);
  border-radius: 4.5px;
  transform: translateY(-0.075em);
  display: grid;
  place-content: center;
}
.login-page__t-c .check input[type=checkbox]::before {
  content: "";
  width: 0.65em;
  height: 0.65em;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--rich-black);
}
.login-page__t-c .check input[type=checkbox]:checked::before {
  transform: scale(1);
  transform-origin: bottom left;
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
}

.login-page__create-button {
  text-align: start;
}
.login-page__create-button button {
  background-color: var(--rich-black);
  color: var(--white);
  border-radius: 7px;
  border: none;
  width: 17rem;
  height: 3.5rem;
  font-size: 1rem;
}